import React from "react";

export default class Finder extends React.Component{
    constructor(props) {
        super(props);

        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);

        this.state = {
            input: "",
            items: ["Пример"]
        };
    }
    handleChange(event){
        this.setState({
            input: event.target.value
        });
    }
    handleClick(){
        debugger;
        if(this.state.input !== "") {
            this.setState({
                items: [...this.state.items, this.state.input],
            });
        }
        else {
            alert("Вы не ввели запрос!");
        }
    }
    linkClick(event){
        this.setState({
           items: this.state.items
        });
    }

    render() {
        return <div>
            Поиск <input type="search" onChange={this.handleChange}/>
            <button onClick={this.handleClick}>Искать</button>
            <ul>{this.state.items.map((item,index) => (
                <li key={index}><a onClick={this.linkClick} alt = {index}  href="#">{item}</a></li>
            ))}</ul>
        </div>
    }
}