import React from "react";
import VideoCard from "./VideoCard";
import {Button} from "@material-ui/core";



export default class VideoList extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            nomer : 0
        }

        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(){
        if(this.state.nomer < this.video.length-1){
            this.setState({
                nomer : this.state.nomer + 1
            });
        }
        else {
            this.setState({
                nomer : 0
            });
        }

    }
    video = ["https://www.youtube.com/embed/Q7FkSWMf2Qo","https://www.youtube.com/embed/cU8gXxF6vqs"
    ,"https://www.youtube.com/embed/lQTJsP8o6S0"];
    render() {

        return <div>
            <VideoCard src = {this.video[this.state.nomer]}/>
            <Button variant="contained" onClick={this.handleClick}>Дальше</Button>
        </div>
    }
}