import React from "react";

export  default class DrinkList extends React.Component{
    constructor() {
        super();

        this.state = {
            error: null,
            isLoaded: false,
            items: []
        }
    }
    componentDidMount() {
        fetch("https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=Cocktail")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result.drinks
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error: true
                    });
                }
            )
    }

    render(){
        const {error, isLoaded,items} = this.state;
        if(error){
            return <p>Ошибка</p>
        }
        else if(!isLoaded){
            return <p>Loading</p>
        }
        else {
            return <ul>
                {items.map( item => (

                   <> <img width="100px" height="100px" src={item.strDrinkThumb}/>
                        <li key={item.name}>
                            {item.strDrink}
                        </li></>
                    )
                )}
            </ul>
        }
    }

}